.galaxy{
    position: relative;
    background-color: #000;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    box-shadow: 0 0 100px #000;
    overflow: hidden;
    animation-name: galaxy;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    animation-direction: alternate-reverse;
    /* transform: perspective(300px) rotateX(30deg); */
}

.sun{
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: orange;
    box-shadow: 0 0 20px yellow;
    top: calc(50vh - 10px);
    left: calc(50vw - 10px);
}

.orbit-mercury{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 1.5%;
    animation-name: move;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-duration: 5s;
}

.mercury{
    position: absolute;
    top: -5px;
    left: 40%;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #888888;
}

.orbit-venus{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 3%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 15s;
}

.venus{
    position: absolute;
    top: -10px;
    left: 40%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #c3862b;
}

.orbit-earth{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 5%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 25s;
}

.earth{
    position: absolute;
    top: -10px;
    left: 40%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #0c00f7;
}

.month{
    margin: 5px 0 0 -20px;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background-color: grey;
    animation-name: month;
    animation-timing-function: ease-in-out;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 1s;
}

.orbit-mars{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 7%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 12s;
}

.mars{
    position: absolute;
    top: -5px;
    left: 40%;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #5f4440;
}

.orbit-jupiter{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 9%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 30s;
}

.jupiter{
    position: absolute;
    top: -15px;
    left: 40%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #754b2c;
}

.orbit-saturn{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 11%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 40s;
}

.saturn{
    position: absolute;
    top: -15px;
    left: 40%;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #c7b491;
    transform: rotate(-45deg);
}

.saturn-line{
    margin: 10px 0 0 -5px;
    width: 35px;
    height: 5px;
    border-radius: 25px;
    background-color: #c3862b;
    animation-name: saturn;
    animation-timing-function: ease-in-out;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    animation-direction: alternate-reverse;
}

.orbit-uranus{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 13%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 50s;
}

.uranus{
    position: absolute;
    top: -10px;
    left: 40%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #cbf0f2;
}

.orbit-neptune{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    padding: 15%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 60s;
}

.neptune{
    position: absolute;
    top: -10px;
    left: 40%;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #282d75;
}

.orbit-pluto{
    position: absolute;
    border-radius: 50%;
    border: 1px solid #fff;
    box-shadow: 0 0 10px #000;
    background-color: #000;
    opacity: .2;
    padding: 17%;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 0ms;
    animation-iteration-count: infinite;
    animation-duration: 20s;
}

.pluto{
    position: absolute;
    top: -5px;
    left: 40%;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: gray;
}

@keyframes galaxy{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.03);
    }
}

@keyframes move{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

@keyframes month{
    0%{
        margin-left: -10px;
    }
    100%{
        margin-left: 20px;
    }
}

@keyframes saturn{
    0%{
        background-color: #c3862b;
    }
    100%{
        background-color: brown;
    }
}

@media(max-width: 900px){
    .orbit-mercury{
        padding: 5%;
    }
    .orbit-venus{
        padding: 10%;
    }
    .orbit-earth{
        padding: 15%;
    }
    .orbit-mars{
        padding: 20%;
    }
    .orbit-jupiter{
        padding: 25%;
    }
    .orbit-saturn{
        padding: 30%;
    }
    .orbit-uranus{
        padding: 35%;
    }
    .orbit-neptune{
        padding: 40%;
    }
    .orbit-pluto{
        padding: 45%;
    }
}